.modal-overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-modal-backdrop) !important;
    overflow: hidden;
    overflow-y: auto;
    z-index: 999;
    opacity: 0;
    transition: opacity 0.3s;
}
.ReactModal__Overlay--after-open{
    opacity: 1;
}
.ReactModal__Overlay--before-close{
    opacity: 0;
}
.modal-overlay::before, .modal-overlay::after{
content: '';
display: block;
height: 1px;
flex-grow: 1;
}
.modal-content{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  max-width: 541px;
  border-radius: 24px;
  background-color: var(--color-wight);
  padding: 40px;
  transform: translateY(50px);
  transition: opacity 0.3s, transform 0.3s;
}
.modal-close-btn{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 16px;
    right: 16px;
    width: 16px;
    height: 16px;
    padding: 0;
    background-color: transparent;
   border: none;
}
.modal-close-icon{
    width: 16px;
    height: 16px;
    fill: var(--color-black);
}